    width: 4rem;
    height: 4rem;
  }
}
.form__button svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 1.6rem;
  height: 1.8rem;
}
@media (min-width: 2000px) {
  .form__button svg {
    width: 16px;
    height: 18px;
  }
}
@media (orientation: portrait) {
  .form__button svg {
    display: block;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button svg {
    display: block;
  }
}

.form--result .form__field {
  width: calc(100% - 31rem);
}
@media (min-width: 2000px) {
  .form--result .form__field {
    width: calc(100% - 310px);
  }
}
@media (orientation: portrait) {
  .form--result .form__field {
    width: calc(100% - 5rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form--result .form__field {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form--result .form__field {
    width: calc(100% - 5rem);
  }
}

