@keyframes prizesItemBOpacity {
    to {
        opacity: 1;
    }
}

@keyframes prizesItemSpanPositionOpacity {
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.prizes__item--journeys .prizes__desc {
    & b {
        width: 68px;
        text-align: right;
    
        animation-name: prizesItemBOpacity;
        animation-duration: 0.3s;
        animation-fill-mode: forwards;
        animation-delay: 2.85s;
    
        opacity: 0;
    }

    & span {
        animation-name: prizesItemSpanPositionOpacity;
        animation-duration: 0.7s;
        animation-fill-mode: forwards;
        animation-delay: 2.7s;

        opacity: 0;
        transform: translateX(40px);
    }
}

.prizes__item--cases .prizes__icon {
    margin-left: -7rem;
}

