.page-mask {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 0;
    width: 100%;
    z-index: 1;
    opacity: 0;

    background-color: #5F458C;
}

.screen.exitingBottomToTop ~ .page-mask {
    opacity: 1;
    height: 100%;
    transition: height 0.5s ease-in-out;
}

.screen.exitingFadeOut ~ .page-mask {
    opacity: 1;
    top: 0;
    height: calc(100% - 27px);
    transition: opacity 0.5s ease-in-out;
}